.page-body {
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: $color-tangaroa;
  background-color: $color-alice-blue;

  @include vp-1023 {
    max-width: 1023px;
  }

  @include vp-767 {
    max-width: 767px;
  }
}
